 import Request from 'luch-request'
 import {setToken} from '../../util/auth.js'
 import devConfig from '../../api/config.js'

export default {
	data(){
		return{
			PageCur: 'basics',
			modelContent: '',
			userMessage: null,
			modalName: null,
			userName: '',
			password: '',
			checkbox: [{
				value: 'A',
				checked: true
			}, {
				value: 'B',
				checked: false
			}],
		}
	},
	onLoad() {
	},
	methods:{
		goLogin:function(){
			var _this = this
			const http = new Request()
			var config = {
				baseURL: devConfig.BASE_API
			}
			http.setConfig((config)=>{
				config.baseURL = devConfig.BASE_API
				return config
			})
			 http.get('/md/login?username=' + _this.userName + '&password=' + _this.password).then(res =>{
				var result = res.data.flag
				if(result=='true'){
					var data = res.data.token
					setToken(data)
					uni.redirectTo({
					    url: 'inspectionIndex'
					});
				}else{
					uni.showToast({
						title:result,
						position: 'bottom',
						icon: 'none',
						duration:1000
					})
				}
			})
		}
	}
}
